  Address,
  AddressIndex,
  AddressIndexAddressIndexOptionsOneOf,
  AggregatedTaxBreakdown,
  ApplicationError,
  CalculateTaxOptions,
  CalculateTaxRequest,
  CalculateTaxResponse,
  JurisdictionType,
  LineItem,
  LineItemTaxDetails,
